<template>
    <div class="vatIn">
      <el-dialog title="" :visible.sync="$store.state.inShow" width="500px" :before-close="handleDialogClose">
        <el-form :model="form">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="form.inNo" placeholder="">
              <el-option v-for="item in inData"
                          :value="item"
                          :label="item">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="closeDialog"> </el-button>
          <el-button type="primary" @click="inCheck"> </el-button>
        </div>
      </el-dialog>
    </div>
</template>

<script>
  import {vatInNo} from '../../common/js/axios/getData';
  export default {
    name: "vatIn",
    props:{
      inData:{
        type:Array,
        default:()=>[]
      }
    },
    data(){
      return{
        formLabelWidth: '80px',
        form:{
          inNo:"",
        }
      }
    },
    mounted(){
    },
    methods:{
      inCheck(){
        //
        let inNo =this.form.inNo;
        vatInNo(inNo).then(res=>{
          //
          this.form.inNo = '';
          this.inList = [];
          //JSON
          let data = JSON.parse(res.data);
          //
          this.$store.state.inShow = false;
          //
          this.$emit('vatCheck',data);
        }).catch(err=>{
          console.log(err)
        })


      },
      closeDialog(){
        //
        this.$store.state.inShow = false;
        //
        this.form.inNo = '';
        //
        this.$emit('cancelShow',false);
      },
      handleDialogClose(){
        this.closeDialog();
      }
    }
  }
</script>

<style scoped>

</style>



